* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.nav-list {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}
.nav-links {
  text-decoration: none;
  color: black;
  font-size: 20px;
}

.home,
.about {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home-content,
.about-content {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  img {
    height: 90%;
  }
  h1 {
    font-size: 50px;
    margin: 30px 0px;
  }
  p {
    font-size: 20px;
  }
}

.home {
  background-image: linear-gradient(
    -225deg,
    #4cc3ff 0%,
    #7c64d5 48%,
    #ff057c 100%
  );
}
.about {
  background-image: linear-gradient(
    -225deg,
    #69eacb 0%,
    #eaccf8 48%,
    #6654f1 100%
  );
}
